@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');

*::before, *::after{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    
}  
  body {
    font-family: 'Gotham';
    font-weight: bolder;
    background: linear-gradient(to right, purple, cyan);
  }
  
  .container {
    max-width: 500px;
    margin: 30px auto;
    overflow: auto;
    min-height: 300px;
    border: 1px solid rgb(255, 255, 255);
    padding: 40px;
    border-radius: 5px;
    
  }
  .header {
    
    display: flex;
    color: white;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    background: none;
  }
  
  
  .btn {
    display: inline-block;
    background: #000;
    color: white;
    border: none;
    padding: 10px 20px;
    margin: 5px;
    border-radius: 5px;
    cursor: pointer;
    text-decoration: none;
    font-size: 15px;
    font-family: "Gotham";
    font-weight: bold;
  }
  
  .btn:focus {
    outline: none;
  }
  
  .btn:active {
    transform: scale(0.98);
  }
  
  .btn-block {
    display: block;
    width: 100%;
  }
  
  .task {
    color: white;
    background: rgba(255, 255, 255, .30);
    margin: 5px;
    padding: 10px 20px;
    cursor: pointer;
    border-radius: 5px;
  }
  
  .task.reminder {
    border-left: 5px solid cyan;
  }
  
  .task h3 {
    
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .add-form {
    margin-bottom: 40px;
    color: white;
  }
  
  .form-control {
    margin: 20px 0;
  }
  
  .form-control label {
    display: block;
  }
  
  .form-control input {
    border-radius: 5px;
    border: none;
    width: 95%;
    height: 30px;
    margin: 5px;
    padding: 3px 7px;
    font-size: 17px;
  }
  
  .form-control-check {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .form-control-check label {
    flex: 1;
  }
  
  .form-control-check input {
    flex: 2;
    height: 20px;
  }
  
  footer {
    margin-top: 30px;
    text-align: center;
  }